import axios from 'axios'
// let base = ' https://cnodejs.org/api/v1'
// let base = 'https://api.xuminjun.com'
// 自动将响应头中的token 放到请求头中去请求
axios.defaults.baseURL = 'http://localhost:3000'
axios.defaults.withCredentials = true
// 登录接口
export const login = params => {
  return axios.post('/login', params)
    .then(res => {
      console.log(res)
      return res.data
    })
    .catch(error => { console.log(error) })
}
// 获取nodejs资讯
export const getTopics = params => { return axios.get('/test').then(res => res.data).catch(error => { console.log(error) }) }
// 获取用户信息
export const getUser = params => { return axios.get('/getUser', {params: params}).then(res => res.data).catch((error) => { console.log(error) }) }
// 获取左侧菜单列表
export const getMenus = params => { return axios.get('/users/getMenus').then(res => res.data).catch(error => { console.log(error) }) }



